<template>
<div class="confirm">
  
  <button class="confirm-yes" @click="logout">Confirm</button>
  <button class="confirm-back" @click="goback">Go back!</button>
</div>
</template>

<script>
export default {
methods:{
    logout(){
      const userData = { email: '', pass: '' };
      localStorage.setItem('user', JSON.stringify(userData));
      this.$router.push("/")
    },
    goback(){
      this.$router.push('/home')
    }
},

}

</script>

<style lang="scss">
.confirm{
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #001414;
  height: 100vh;
  gap: 100px;
  &-yes,&-back{
   padding: 20px 40px;
   background: rgba(128, 128, 128, 0.664);
   border: 2px white solid;
   color: rgba(255, 255, 255, 0.65);
   transition: 0.5s;
   cursor: pointer;
   font-family: 'Libre';
   font-size: 18px;
  }
  &-yes:hover{
    padding: 25px 45px;
    color: white;
  
  }
  &-back:hover{
    padding: 25px 45px;
    color: white;
  }
  
}
</style>