<template>
<section class="login">
  <div class="login-box">
    <h1>Log in please!</h1> 
    <p class="emailexample">You can use these email with password</p>
    <p class="emailexample">email: 'test@test.com',</p>
    <p class="emailexample">  pass: 'test12345'</p> 
    <form class="login-box__form">
      <h3>Email</h3>
      <input type="email" placeholder="Enter e-mail" v-model="username">
      <h3>Pass</h3>
      <input type="password" placeholder="Enter password" v-model="pass">
      <button @click="checkUsername">Login</button>
      <span>{{this.invalidCredentials}}</span>
    </form>
  </div>
</section>
</template>

<script type="module">
 import { users } from "@/views/db"
 import { disabledItems } from "@/pages/Home.vue"
 import axios from 'axios';

 export default {
  
  data() {
    return {
       counter: 0,
       username: '',
       pass: '',
    }
  },
  
  methods: {
    checkUsername() {
      
       for (const key in users) {
         const user = users[key];
       
        if ( this.username == user.email  && this.pass == user.pass ) {
          // console.log('Credentials matched for user:', key);
          const userData = { email: this.username, pass: this.pass };
          localStorage.setItem('user', JSON.stringify(userData));
          console.log('OK');
          axios.get(this.counter = 1)
          this.$emit('counter', this.counter);
          this.$router.push('/home');
          
          return;
        }else{
          let invalidCredentials = "Invalid credentials"
        }
      }

    },
    
     checkAuth() {
       
     if (!localStorage.getItem('user')) {
  // code to be executed if local storage does not contain user
      const userData = { email: '', pass: '' };
      localStorage.setItem('user', JSON.stringify(userData));
      console.log(userData); 
     }
       
      const storedUser = localStorage.getItem("user");
      const user = JSON.parse(storedUser);
      let customerWithEmail = null;
      
      for (const key in users) {
        if (users.hasOwnProperty(key) && users[key].email === user.email) {
          customerWithEmail = users[key];
          break;
        }else{
          
        }
      }

      if (customerWithEmail) {
        if (customerWithEmail.pass === user.pass) {
          this.$router.push("/home")
        } else {
          this.errorMessage = "Invalid password";
          this.$router.push("/");
        }
      } else {
        this.errorMessage = "Invalid email";
        this.$router.push("/");
      }
    },
    
    
  },
 
created(){
  this.checkAuth()
  this.checkUsername()
   
}

}
</script>

