<template>
<section class="login">
  <div class="login-box">
    <h1>Register please!</h1> 
    <form class="login-box__form">
      <h3>Email</h3>
      <input type="email" placeholder="Enter e-mail" v-model="username">
      <h3>Pass</h3>
      <input type="password" placeholder="Enter password" v-model="pass">
      <button @click="registration">Register</button>
      
      
    </form>
  </div>
</section>
</template>

<script>
import { users } from "@/views/db"
export default {
  data() {
    return {
      username: '',
      pass: '',
      user4: {email:'', pass:''},
    }
  },
  
  methods: {
    registration(){
      this.user4.email = this.username
      this.user4.pass = this.pass
      users['user4'] = this.user4
      
    }
  },
}
</script>