<script>
export default {

  mounted(){
   let burgerButton = document.querySelector('.burger-button')
    let burgerMenu = document.querySelector('.burger-menu')
    let counterBurger = 1;
    let documentWeb = document.querySelector('body')
    burgerButton.addEventListener('click', function burger(event) {
      burgerMenu.style.transition = '0s' 
      if (counterBurger == 1) {
        documentWeb.style.overflow = 'hidden'
        burgerMenu.style.display = 'flex'
        burgerMenu.style.position = 'absolute'
        burgerButton.style.right = '20px'
         burgerButton.style.top = '80px'
        //  burgerMenu.style.top = '0'
        burgerMenu.style.transform = 'translateX(0)'
        counterBurger--
      } else if(counterBurger == 0) {
        documentWeb.style.overflow = 'visible'
        burgerMenu.style.display = 'none'
        burgerButton.style.right = '20px'
        burgerButton.style.top = '30px'
        burgerMenu.style.transform = 'translateX(800px)'
        burgerMenu.style.position = 'relative'
        counterBurger++
      }
    })
    document.addEventListener('click', function(event) {
    if (event.target.classList.contains('navbar-nav__ul2---item___link')) {
      documentWeb.style.overflow = 'visible';
     
    }
  });
  },
 
}
</script>

