<script>
import NavigationBar from "@/components/NavigationBar.vue";
 import { users } from "@/views/db"
export default {
  components: { NavigationBar },
  data() {
    return {
       counter: 0,
       username: '',
       pass: '',
       };
  },
  mounted() {
    let burgerButton = document.querySelector('.burger-button')
    let burgerMenu = document.querySelector('.burger-menu')
    let counterBurger = 1;
    let documenWeb = document.querySelector('body')
    burgerButton.addEventListener('click', () => {

      if (counterBurger == 1) {
        burgerMenu.style.display = 'flex'
        burgerMenu.style.position = 'absolute'
        burgerButton.style.right = '20px'
         burgerMenu.style.top = '0'
        burgerMenu.style.transform = 'translateX(0)'
        documenWeb.style.overflow = 'hidden'
        counterBurger--
      } else if(counterBurger == 0) {
        burgerMenu.style.display = 'none'
        burgerButton.style.right = '20px'
        burgerMenu.style.transform = 'translateX(800px)'
        burgerMenu.style.position = 'relative'
        documenWeb.style.overflow = 'visible'
        counterBurger++
      }
    })
