            </h1>
            <p>
              In 2021, Idaho launched tools that provide line staff with key metrics, like
              timely assessments and contacts. Idaho’s email alerts are tailored to
              individual officers’ caseloads, showing them how their results compare to
              their historical performance and to officers with similar caseloads around
              the state. The tool also provides staff with alerts for high-priority
              developments, like supervision-level mismatches, helping them to quickly
              course-correct, improve with feedback, and celebrate successes.
            </p>
          </div>
          <div class="slider-text" style="opacity: 0; ">
            <h1>Idaho’s line staff dashboard helps officers plan their days.</h1>
            <p>
              The line staff dashboard is a forward-looking, actionable tool that offers
              insights to help probation and parole officers allocate their time on a
              daily basis. The tool provides a holistic overview of an officer’s caseload,
              automatically surfacing opportunities to address client needs, like overdue
              contacts, supervision-level mismatches, and earned discharge opportunities.
