          </div>
          <div class="slider-text" style="opacity: 0;">
            <h1>
              Population projections inform facility management and departmental policy
              priorities.
            </h1>
            <p>
              Idaho’s case-level population projection model forecasts incarcerated and
              supervised population levels over time. The microsimulation model uses
              patterns in historical data and sentencing information to determine how
              people flow through the system and to project expected changes. These
              projections allow for proactive facility management, reveal trends driving
              population changes that may inform department-level reforms, improve budget
