<script>
import NavigationBar2Type from "@/components/NavigationBar2Type.vue";
import Wherewework from "@/components/Wherewework.vue";
import Footer from "@/components/Footer.vue";
import Dashboards from "@/components/Dashboards.vue";

export default {
  components: { NavigationBar2Type, Wherewework, Footer, Dashboards },
  mounted() {
    const images = document.querySelectorAll(".image");
    const dots = document.querySelectorAll(".dot");
    const sliderText = document.querySelectorAll(".slider-text");

    dots.forEach((dot, index) => {
      dot.addEventListener("click", () => {
        images.forEach((image) => (image.style.opacity = 0));
        sliderText.forEach((text) => (text.style.opacity = 0));
        dots.forEach((dot) => (dot.style.background = "rgba(34, 34, 34, 0.4)"));

        images[index].style.opacity = 1;
        sliderText[index].style.opacity = 1;
        dots[index].style.background = "#000000";

        dots.forEach((dot) => (dot.disabled = true));
        setTimeout(() => dots.forEach((dot) => (dot.disabled = false)), 1500);
      });
    });
  },
};
</script>

